import { makeStyles, Typography } from '@material-ui/core';
import { graphql, useStaticQuery } from 'gatsby';
import GatsbyImage from 'gatsby-image';
import React, { useEffect } from 'react';
import Layout from 'src/components/Layout/Page';
import { FluidImageSharp } from 'src/models/image';
import { logger } from '@lubycon/logger';

const useStyles = makeStyles({
  root: {
    height: '80vh',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    flexDirection: 'column',
  },
  image: {
    width: '100%',
    maxWidth: 800,
  },
});

const pageNotFoundPageLogger = logger.getPageLogger('404_page');

const NotFoundPage = () => {
  const styles = useStyles();
  const { errorImage } = useStaticQuery<{ errorImage: FluidImageSharp }>(graphql`
    query {
      errorImage: file(absolutePath: { regex: "/error.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  useEffect(() => {
    pageNotFoundPageLogger.view();
  }, []);

  return (
    <Layout pageName="페이지를 찾을 수 없습니다">
      <div className={styles.root}>
        <GatsbyImage className={styles.image} fluid={errorImage.childImageSharp.fluid} />
        <Typography variant="h5">길을 잃어버렸네요...뒤로 돌아가세요!</Typography>
      </div>
    </Layout>
  );
};

export default NotFoundPage;
